import React, { useEffect } from 'react'
import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';

const AlertPopup: React.FC<ErrorPopupPropsType> = ({message, serverRequestSuccess}) => {

    const viewMessage = () => {
        if(message === 'user not found /--\\') return 'user not found'
        if(message === 'not correct password /--\\') return 'not correct password'
        if(message === 'not valid email/password /--\\') return 'not valid email/password'
        if(message === 'email already exists /\\') return 'that username is taken'
        if(message === 'Email address not found /--\\') return 'email adress not found'
        if(message === 'no resetPasswordToken, Check your request! /--\\') return 'please check your email and click on recovery link'
        if(message === 'not your CardsPack! /\\') return 'not your cards pack'
        return 'server error'
    }


    //  useEffect   2 
    useEffect(() => {
        if(message){
            toast(viewMessage().toUpperCase())
        }
        if(serverRequestSuccess){
            toast('success '.toUpperCase())
        }
    }, [message, serverRequestSuccess])
  
    return (
        <div>
            <ToastContainer  
            position="bottom-right"
            autoClose={4000}
            hideProgressBar
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover/>
        </div>
    )
}

export default AlertPopup

// types
type ErrorPopupPropsType = {
    message?: string
    serverRequestSuccess?: boolean | string
}